import {Backdrop, CircularProgress, Container, Typography } from '@material-ui/core';
import { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Tiedot from './components/Tiedot';
import UusiMatka from './components/UusiMatka';
import Header from './components/Header';
import Footer from './components/Footer';

function App() {

  const [matka, setMatka] = useState({
                                      kohde: "",
                                      valuutta: "",
                                      lahtopaiva: 0,
                                      paluupaiva: 0,
                                      budjetti: "",
                                      valuutanarvo: 0,
                                      tiedotTallennettu: false
  });

  const [valuutat, setValuutat] = useState({ 
                                              valuuttakurssit : [],
                                              virhe : null,
                                              tiedotHaettu : false
                                            
  });

  const haeMatkanTiedot = () => {
    if(localStorage.getItem("matka")) {
      setMatka(JSON.parse(localStorage.getItem("matka")));
    } else {
      setMatka();
      console.log(matka);
    }
  }

  const tallennaMatka = () => {
    localStorage.setItem("matka", JSON.stringify(matka));
  }

  const haeValuuttojenTiedot = async () => {
    try {
      const yhteys = await fetch("https://api.ratesapi.io/api/latest");
      const tiedot = await yhteys.json();

      setValuutat({
                      ...valuutat,
                      valuuttakurssit : tiedot.rates,
                      tiedotHaettu : true
      });

      

    } catch (error) {
      setValuutat({
                    ...valuutat,
                    valuuttakurssit : [],
                    tiedotHaettu : true,
                    virhe : `Virhe: yhteyttä valuuttapalvelimelle ei voitu muodostaa. (${error})`
      });

    }
  }
 
  useEffect(() => {
    haeValuuttojenTiedot();
    haeMatkanTiedot();
  }, [])

  useEffect(() => {
    tallennaMatka();
  }, [matka]);



  return (
    <Router>
   <Container>
     <Header>Matkasuunnitelma</Header>
        <Route path="/" exact>
          <Tiedot matka={matka} setMatka={setMatka} valuutat={valuutat.valuuttakurssit}/>
        </Route>
        <Route path="/matkasuunnitelma" exact>
          <Tiedot matka={matka} setMatka={setMatka} valuutat={valuutat.valuuttakurssit}/>
        </Route>
        <Route path="/uusimatka">
          <UusiMatka matka={matka} setMatka={setMatka} valuutat={valuutat.valuuttakurssit}/>
        </Route>

        {(valuutat.virhe)
        ? <Typography color="error">
          {valuutat.virhe}
          </Typography>
        : (valuutat.tiedotHaettu)
          ? <Typography></Typography>
          : <Backdrop open={true}>
            <CircularProgress color="inherit"/>
          </Backdrop>
        }
        <Footer/>
   </Container>
   </Router>
  );
}

export default App;
