import { Container, makeStyles, Typography } from "@material-ui/core"

const useStyles = makeStyles(() => ({
    tausta: {
        background: 'linear-gradient(45deg, #e6fff2 30%, #E0FFFF 90%)',
        minHeight: "80px",
        paddingTop:"20px"
        },
        tekstikentta: {
            fontSize:"12pt",
            textAlign: "center",
            color: "#003333"
    }
}))

function Footer() {
    const tyylit = useStyles();

    return (
        <Container 
        className={tyylit.tausta} 
        maxWidth="md"
        >
        <Typography 
        className={tyylit.tekstikentta}> 
        © 2021 H. Hyvärinen <br/>
        Photos from Pixabay.com</Typography>
        </Container>
    );
}

export default Footer;