import { Button, Container, Dialog, DialogContent, makeStyles, Typography } from "@material-ui/core";
import { Link } from 'react-router-dom';
import { differenceInDays, format } from 'date-fns';
import { useState } from "react";

const useStyles = makeStyles(() => ({
    tausta: {
        backgroundColor: "#F5FFFA",
        minHeight: "120px",
        paddingTop:"20px",
        paddingLeft:"70px"
        },
        tekstikentta: {
            fontSize:"16pt",
            paddingLeft:"20px",
            color: "#003333"
        },
        tekstikentta2: {
            fontSize:"22pt",
            paddingLeft:"20px",
            paddingTop:"20px",
            color: "#003333"
        },
        button: {
            fontSize:"12pt",
            padding:"10px", 
            margin:"20px",
            background: 'linear-gradient(45deg, #e6fff2 30%, #E0FFFF 90%)',
            boxShadow: '0 3px 5px 2px #F5F5F5',
            color: "#003333"
    }
}))

function Tiedot(props) {
    const tyylit = useStyles();
    const poistaMatka = () => {
        let uusiMatkaApu = {
                            kohde: "",
                            valuutta: "",
                            lahtopaiva: 0,
                            paluupaiva: 0,
                            budjetti: "",
                            valuutanarvo: 0,
                            tiedotTallennettu: false
        }

        props.setMatka(uusiMatkaApu);
    }
    
    const [open, setOpen] = useState(false);

    const handleClickOpen = () => {
        setOpen(true);
      };
      const handleClose = () => {
        setOpen(false);
      };
    
    return(
        <Container className={tyylit.tausta} maxWidth="md">
            {(props.matka.tiedotTallennettu)
            ? <>
            <Typography className={tyylit.tekstikentta}>
                Matkakohde: {props.matka.kohde} <br/>
                Lähtöpäivä: {format(props.matka.lahtopaiva,"d.M.y HH:mm")}<br/>
                Paluupäivä: {format(props.matka.paluupaiva,"d.M.y HH:mm")}<br/>
                Matkan kesto: {differenceInDays(props.matka.paluupaiva, props.matka.lahtopaiva)} päivää<br/>
                Kohdevaluutta: {props.matka.valuutta}<br/>
                Budjetti: {props.matka.budjetti} € 
                {(props.matka.valuutta) 
                    ? <>  / {(props.matka.budjetti * props.matka.valuutanarvo).toFixed(2)} {props.matka.valuutta} <br/></>
                    : <br/>
                }
                </Typography>
                <Typography className={tyylit.tekstikentta2}>Lähtöön on vielä {differenceInDays(props.matka.lahtopaiva, new Date())} päivää. Hyvää matkaa!</Typography>
           
            <Button
                component={Link}
                to="/uusimatka"
                variant="contained"
                size="large"
                className={tyylit.button}
                >Muokkaa matkan tietoja
            </Button>
            <Button
                variant="contained"
                size="large"
                className={tyylit.button}
                onClick={handleClickOpen}
                >Poista matka
            </Button>
            <Dialog 
            open={open}
            onClose={handleClose}>
               <DialogContent>Haluatko varmasti poistaa matkan?</DialogContent> 
               <Button onClick={poistaMatka} color="primary">Kyllä</Button>
               <Button onClick={handleClose} color="primary" autoFocus>Ei</Button>
            </Dialog>
            </>
            :<>
            <Typography className={tyylit.tekstikentta}>Aloita lisäämällä matkan tiedot</Typography>
            <Button
                component={Link}
                to="/uusimatka"
                variant="contained"
                size="large"
                className={tyylit.button}
            >Lisää uusi matka</Button>
            </>
}
            
        </Container>
        
    );

}

export default Tiedot;