import { Container, makeStyles, Typography } from "@material-ui/core";
import beach from "./photos/beach.jpg";

const useStyles = makeStyles(() => ({
    tausta: {
            minHeight: "200px",
            paddingTop:"20px",
            backgroundImage: `url(${beach})`
        },
        tekstikentta: {
            paddingTop:"20px",
            fontSize:"36pt",
            textAlign: "center"   ,
            color: "#003333"         
    }
}))

function Header(props) {
    const tyylit = useStyles();

    return(
        <Container 
        className={tyylit.tausta}
        maxWidth="md">
        <Typography className={tyylit.tekstikentta}>{props.children}</Typography>
        </Container>
    );
}

export default Header;