import { Button, Container, FormControl, InputLabel, makeStyles, MenuItem, Select, TextField, Typography } from "@material-ui/core";
import { MuiPickersUtilsProvider, DateTimePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { useEffect, useRef, useState } from "react";
import { Link, useHistory } from 'react-router-dom';

const useStyles = makeStyles(() => ({
    tausta: {
        backgroundColor: "#F5FFFA",
        minHeight: "120px",
        paddingTop:"20px",
        paddingLeft:"70px"
        },
        tekstikentta: {
            fontSize:"16pt",
            paddingLeft:"20px"
        },
        button: {
            padding:"10px", 
            margin:"20px",
            backgroundColor: "#e6fff2"
        }
}))

function UusiMatka(props) {
    const tyylit = useStyles();
    const [pvmaika, setPvmaika] = useState(null);
    const [paluupvmaika, setPaluuPvmaika] = useState(null);
    const [kohdevaluutta, setKohdevaluutta] = useState("");
    const lomaketiedot = useRef({});
    const [virheilmoitus, setVirheilmoitus] = useState({});

    const history = useHistory();

    const tuoMatkanTiedot = () =>
    {
        lomaketiedot.current.kohde = props.matka.kohde;
        lomaketiedot.current.budjetti = props.matka.budjetti;
        if(props.matka.lahtopaiva) {
            setPvmaika(props.matka.lahtopaiva);
        }
        if(props.matka.paluupaiva) {
            setPaluuPvmaika(props.matka.paluupaiva);
        }
        if(props.matka.valuutta){
            setKohdevaluutta(props.matka.valuutta);
        }

    }
    
    const kasitteleLomake = (e) => {
        e.preventDefault();
        let virheet = {};

        if(!lomaketiedot.current.kohde) {
            virheet = {...virheet, kohde: "Matkakohde puuttuu"}
        }
        
        if(!lomaketiedot.current.budjetti || isNaN(lomaketiedot.current.budjetti) ) {
            virheet = {...virheet, budjetti: "Budjetti puuttuu tai on virheellinen"}
        } 

        if(!pvmaika) {
            virheet = {...virheet, pvmaika: "Anna lähtöpäivämäärä"}
        }
        if(!paluupvmaika) {
            virheet = {...virheet, paluupvmaika: "Anna paluupäivämäärä"}
        }

        if (Object.entries(virheet).length > 0) {

            setVirheilmoitus({...virheet});
  
        } else {
            setVirheilmoitus({});
            let uusiMatkaApu = {
                            kohde: lomaketiedot.current.kohde,
                            valuutta: kohdevaluutta,
                            valuutanarvo: props.valuutat[kohdevaluutta],
                            lahtopaiva: pvmaika,
                            paluupaiva: paluupvmaika,
                            budjetti: lomaketiedot.current.budjetti,
                            tiedotTallennettu: true, 
                            jaljella: 0,
                            kulut: 0
            }

            props.setMatka(uusiMatkaApu);
            history.push("/");
        }
    }

    useEffect(() => {
        tuoMatkanTiedot();
      }, [])
    
    const syoteKasittelija = (e) => {

        let arvo = e.target.value;
        lomaketiedot.current[e.target.name] = arvo;
    
      }

    return (
        <Container
        className={tyylit.tausta}
        maxWidth="md">
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
        <form onSubmit={kasitteleLomake} >
            <Typography>Lisää uusi matka</Typography>
            <TextField
                name="kohde"
                label="Matkakohde"
                variant="outlined"
                fullWidth
                defaultValue={props.matka.kohde}
                style={{marginBottom : 10}}
                error={Boolean(virheilmoitus.kohde)}
                helperText={virheilmoitus.kohde}
                onChange={ syoteKasittelija }
            />
            <DateTimePicker
                clearable
                label="Lähtöpäivä"
                inputVariant="outlined"
                fullWidth={true}
                cancelLabel="Peruuta"
                ampm={false}
                value={pvmaika}
                format="d.M.y HH:mm"
                onChange={setPvmaika}
                error={Boolean(virheilmoitus.pvmaika)}
                helperText={virheilmoitus.pvmaika}
                style={{marginBottom : 10}}
            />
            <DateTimePicker
                clearable
                label="Paluupäivä (lähtöpäivä on annettava ensin)"
                inputVariant="outlined"
                fullWidth={true}
                cancelLabel="Peruuta"
                ampm={false}
                value={paluupvmaika}
                disabled={!pvmaika}
                minDate={pvmaika}
                format="d.M.y HH:mm"
                onChange={setPaluuPvmaika}
                error={Boolean(virheilmoitus.paluupvmaika)}
                helperText={virheilmoitus.paluupvmaika}
                style={{marginBottom : 10}}
            />

            <TextField
                name="budjetti"
                label="Budjetti (syötä budjetti ainoastaan numeroina)"
                variant="outlined"
                fullWidth         
                style={{marginBottom : 10}}
                defaultValue={props.matka.budjetti}
                error={Boolean(virheilmoitus.budjetti)}
                helperText={virheilmoitus.budjetti}
                onChange={ syoteKasittelija }
            />
            <FormControl
                variant="outlined"
                className={tyylit.syote}
                fullWidth
                >
                <InputLabel
                    style={{
                    paddingLeft : 5,
                    paddingRight: 5}}
                >Kohdevaluutta (ei pakollinen)</InputLabel>
                <Select
                    value={kohdevaluutta}
                    defaultValue={props.matka.valuutta}
                    displayEmpty
                    onChange={(e) => { setKohdevaluutta(e.target.value)}}
                    >
                    {Object.keys(props.valuutat).sort().map((valuutta) => {
                    return <MenuItem key={valuutta} value={valuutta}>{valuutta} ({props.valuutat[valuutta]})</MenuItem>        
                    })}   
                </Select>
            </FormControl>
            <Button 
                type="submit"
                variant="contained"
                fullWidth
                style={{marginBottom : 10}}
                className={tyylit.button}
            >Tallenna matka</Button>

            <Button 
                component={Link}
                to="/"
                variant="contained"
                fullWidth
                className={tyylit.button}
            >
                Peruuta
            </Button>     

            </form>
    </MuiPickersUtilsProvider>
    </Container>
    );
}

export default UusiMatka;